/** @jsx jsx */
/* eslint no-unused-vars: 0 */
import React, { useState } from 'react';
import { jsx, useThemeUI } from 'theme-ui';
import { Flex, Box, Heading, Text, Button } from 'rebass';
import {
  CarouselProvider,
  Slider,
  Slide,
  ButtonBack,
  ButtonNext,
  DotGroup,
} from 'pure-react-carousel';
import 'pure-react-carousel/dist/react-carousel.es.css';
import './carousel.css';
import styled from '@emotion/styled/macro';

import ShopifyBackgroundImage from '../../components/ShopifyBackgroundImage';
import ChevronLeft from '../../components/Icons/ChevronLeft';
import ChevronRight from '../../components/Icons/ChevronRight';
import strings from './strings.json';

const { ariaNextButtonLabel, ariaBackButtonLabel, ariaSliderButton } = strings;

const Image = styled.img`
  width: 100%;
`;

const Flag = styled(Box)`
  content: "";
  position: absolute;
  background: transparent;
  left: 0px;
  transform: translate(0px, 0px);
  width: inherit;
  height: 0px;
  bottom: 0px;
  border-left: 50vw solid white;
  border-right: 50vw solid white;
`;

const MainPageCarousel = (props) => {
  const { carousel, data } = props;

  const {
    theme: { breakpoints },
  } = useThemeUI();

  let naturalSlideHeightMediaQuery;

  if (typeof window !== `undefined`) {
    naturalSlideHeightMediaQuery =
      window && window.matchMedia(`(max-width: ${breakpoints[1]})`);
    naturalSlideHeightMediaQuery.addEventListener('change', (e) => {});
  }

  const [naturalSlideHeight, setNaturalSlideHeight] = useState(
    naturalSlideHeightMediaQuery && naturalSlideHeightMediaQuery.matches
      ? 100
      : 60
  );

  if (typeof window !== `undefined`) {
    naturalSlideHeightMediaQuery.addEventListener('change', (e) => {
      setNaturalSlideHeight(e.matches ? 100 : 60);
    });
  }

  return (
    <Box
      bg="transparent"
      sx={{
        position: 'relative',
        textAlign: 'center',
        display: ['none', 'block'],
      }}
    >
      <CarouselProvider
        naturalSlideWidth={100}
        naturalSlideHeight={naturalSlideHeight}
        totalSlides={carousel.children.length}
        isPlaying
        infinite
      >
        <Slider>
          {carousel.children.map((slide, index) => {
            if (slide.type === 'slide') {
              return (
                <Slide key={index} index={index}>
                  <ShopifyBackgroundImage
                    src={slide.bgImageSrc}
                    maxSize="800"
                    display="flex"
                    flexDirection={["column", "row"]}
                    textColor={
                      slide.textColor ? slide.textColor : undefined
                    }
                    textBgColor={
                      slide.textBgColor ? slide.textBgColor : undefined
                    }
                    buttonText={
                      slide.buttonText ? slide.buttonText : undefined
                    }
                    buttonTextColor={
                      slide.buttonTextColor
                        ? slide.buttonTextColor
                        : undefined
                    }
                    buttonBgColor={
                      slide.buttonBgColor
                        ? slide.buttonBgColor
                        : undefined
                    }
                  >
                    <Box width={[1, 1 / 2]} order={[2, 1]}>
                      <Heading fontSize={["5vw", "2vw", "1.5vw"]} mb=".5em">{slide.header}</Heading>
                      <Text>{slide.subheader}</Text>
                      <Button variant="primary" to={slide.link}>{ariaSliderButton}</Button>
                    </Box>
                    <Box width={[1, 1 / 2]} order={[1, 2]}>
                      <Image src={slide.imageSrc} alt={slide.handle} />
                    </Box>
                  </ShopifyBackgroundImage>
                </Slide>
              );
            } else {
              return '';
            }
          })}
        </Slider>

        <ButtonBack
          aria-label={ariaBackButtonLabel}
          sx={{
            display: ['none', 'block'],
            position: 'absolute',
            bottom: ['36%', '50%'],
            left: 1,
            'z-index': '2',
            border: 0,
            bg: 'transparent',
            color: 'primary',
            py: 2,
            px: [0, 3],
          }}
        >
          <ChevronLeft width="40px" height="40px" />
        </ButtonBack>

        <ButtonNext
          aria-label={ariaNextButtonLabel}
          sx={{
            display: ['none', 'block'],
            position: 'absolute',
            bottom: ['36%', '50%'],
            right: 1,
            'z-index': '2',
            border: 0,
            bg: 'transparent',
            color: 'primary',
            py: 2,
            px: [0, 3],
          }}
        >
          <ChevronRight width="40px" height="40px" />
        </ButtonNext>

        <DotGroup
          sx={{
            display: ['none', 'block'],
            position: 'absolute',
            bottom: '10%',
            left: '50%',
            transform: 'translate(-50%)',
            'z-index': '2',
            border: 0,
            bg: 'transparent',
            color: 'primary',
            py: '2',
            px: '3',
          }}
        />
      </CarouselProvider>

      <Flag
        sx={{
          borderTopWidth: [25, 50, 75],
          borderTopStyle: 'solid',
          borderTopColor: 'transparent'
        }}
      />
    </Box>
  );
};

export default MainPageCarousel;
