/** @jsx jsx */
/* eslint no-unused-vars: 0 */
import React, { useState } from 'react';
import { jsx, useThemeUI } from 'theme-ui';
import { Flex, Box, Text } from 'rebass';
import styled from '@emotion/styled/macro';
import Instagram from '../../components/Icons/Instagram';

import strings from './strings.json';

const { instagramHeader } = strings;

const Image = styled.img`
  width: 100%;
`;

const MainPageInstagram = (props) => {
  const {
    theme: { breakpoints },
  } = useThemeUI();

  const { instagram } = props;
  const sectionItemsNumber =
    instagram && instagram.children && instagram.children.length > 0
      ? instagram.children.length
      : 1;
  const sectionHeight = sectionItemsNumber < 3 ? '60vh' : '40vh';

  return (
    <Box bg="background" textAlign="center" mx="auto" p={["5vw", "3.5vw"]}>
      <Flex justifyContent="center" mb="1em">
        <Instagram mr="1em" />
        <Text fontSize={["2em", "3vw", "2vw"]} lineHeight="title">{ instagramHeader }</Text>
      </Flex>
      <Flex flexWrap="wrap">
        {instagram.children.map((block, index) => {
          if (block.type === 'igImage' && index <= 3) {
            return (
              <Box
                bg="background"
                width={[1 / 2, 1 / 4, 1 / sectionItemsNumber]}
                key={index}
                height={"auto"}
                p={[".3em", "1em"]}
                textAlign="center"
              >
                <Image src={block.imageSrc} alt={block.handle} />
              </Box>
            );
          } else if (block.type === 'igImage' &&  index == 4) {
            return (
              <Box
                display={["none", null, "block"]}
                bg="background"
                width={[1, 1 / sectionItemsNumber]}
                key={index}
                height={"auto"}
                p={[".3em", "1em"]}
                textAlign="center"
              >
                <Image src={block.imageSrc} alt={block.handle} />
              </Box>
            );
          } else {
            return '';
          }
        })}
      </Flex>
    </Box>
  );
};

export default MainPageInstagram;
